
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  background: #171711;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
}
a {
  color: lightgreen;
}
button {
  background: none;
  border: solid 3px white;
  border-radius: .5rem;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  padding: .5rem;
  -webkit-transition: all .3s;
  transition: all .3s;
}
button:hover {
  background: white;
  color: #2c3e50;
}
#challenge-info-container {
  border-top: solid 1px white;
  margin: auto;
  max-width: 40rem;
}
@media all and (max-width: 560px) {
#challenge-info-container {
    max-width: 90%;
}
}













h1[data-v-00ceedce], h2[data-v-00ceedce] {
  font-weight: normal;
}
ul[data-v-00ceedce] {
  list-style-type: none;
  padding: 0;
}
li[data-v-00ceedce] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-00ceedce] {
  color: #42b983;
}

#button-get-challenge {
  margin-bottom: 1rem;
}
#challenge-info-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
figure {
  width: 50%;
}
figure > img {
  margin: auto;
  width: 100%;
}
figure > p {
  background: white;
  color: black;
  display: block;
  margin: 0;
  padding: 1rem;
  width: 100%;
}

